@import "vars";
@import "libs";
@import "fonts";

::placeholder {
	color: #9e9e9e; }
html, body {
	height: 100%; }
.main {
	flex: 1 0 auto; }
html {
	font-size: 10px; }
body {
	font-size: 1.4rem;
	min-width: 320px;
	position: relative;
	line-height: 1.43;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	display: flex;
	flex-direction: column;
	font-weight: 500;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; } }
b, strong {
	font-weight: 700; }
p {
	margin-bottom: 0; }
a {
	color: $text;
	text-decoration: none;
	transition: 0.25s;
	&:hover {
		color: inherit;
		text-decoration: underline; }
	&[href^="#"], &[href^="tel"], &[href^="mail"] {
		&:hover {
			text-decoration: none; } }
	&.link-decorated {
		text-decoration: underline;
		font-size: 1.7rem;
		&:hover {
			text-decoration: none; } } }
.w-100 {
	width: 100%; }
.h-100 {
	height: 100%; }
.list {
	&-unstyled {
		@include list-unstyled; } }
.icon {
	display: inline-block;
	vertical-align: middle;
	top: -0.3rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-left {
		margin-right: 1rem; }
	&-right {
		margin-left: .8rem; }
	&-favorite {
		@include size(2.3rem, 2.1rem);
		background-image: url(../img/icon/icon-favorite.png);
		&_l {
			@include size(2.7rem, 2.4rem);
			background-image: url(../img/icon/icon-favorite_l.png); }
		&_w {
			@include size(2.2rem, 2rem);
			background-image: url(../img/icon/icon-favorite_w.png); } }
	&-search {
		@include size(2.1rem);
		background-image: url(../img/icon/icon-search.png); }
	&-login {
		@include size(1.7rem, 2rem);
		background-image: url(../img/icon/icon-login.png); }
	&-send {
		@include size(2rem, 1.7rem);
		background-image: url(../img/icon/icon-send.png); }
	&-share {
		@include size(1.8rem, 2rem);
		background-image: url(../img/icon/icon-share.png); }
	&-star {
		@include size(2rem);
		background-image: url(../img/icon/icon-star.png);
		&_b {
			@include size(2.1rem, 2rem);
			background-image: url(../img/icon/icon-star_b.png); } }
	&-down {
		@include size(1.5rem, .8rem);
		background-image: url(../img/icon/icon-down.png); } }
.image {
	display: block;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	&-con {
		background-size: contain; }
	&-cov {
		background-size: cover; }
	&-hover {
		overflow: hidden;
		cursor: pointer;
		&:before {
			content: '';
			@include size(100%);
			background: inherit;
			position: absolute;
			top: 0;
			left: 0;
			transition: 0.4s; }
		&:hover:before {
			transform: scale(1.05, 1.05); } } }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: 3rem;
	line-height: 1.2;
	font-weight: 700; }
h1, .h1 {
	font-size: 3.4rem; }
h2, .h2 {
	font-size: 3rem; }
h3, .h3 {
	font-size: 2.6rem; }
h4, .h4 {
	font-size: 2.2rem; }
h5, .h5 {
	font-size: 1.8rem; }
h6, .h6 {
	font-size: 1.6rem;
	margin-bottom: 1.3rem; }
.text {
	&-xs {}
	&-s {}
	&-m {}
	&-l {}
	&-xl {} }
.btn {
	display: flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	text-decoration: none!important;
	&:after {
		content: "";
		display: inline-block;
		min-height: inherit;
		visibility: hidden; }
	&-l {
		height: 4rem;
		min-height: 4.8rem;
		font-size: 1.5rem; }
	&-black {
		background-color: #000000;
		color: #fff;
		&:hover {
			background-color: lighten(#000, 20%); } } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.row {
	&-xs {
		@include make-row(.6rem);
		> * {
			padding: {
				left: .3rem;
				right: .3rem; } } }
	&-s {
		@include make-row(1.4rem);
		> * {
			padding: {
				left: .7rem;
				right: .7rem; } } }
	&-m {
		@include make-row(3rem);
		> * {
			padding: {
				left: 1.5rem;
				right: 1.5rem; } } }
	&-l {
		@include make-row(4rem);
		> * {
			padding: {
				left: 2rem;
				right: 2rem; } } }
	&-xl {
		@include make-row(4.4rem);
		> * {
			padding: {
				left: 2.2rem;
				right: 2.2rem; } } }
	&-xxl {
		@include make-row(6rem);
		> * {
			padding: {
				left: 3rem;
				right: 3rem; } } } }
.form {
	display: block;
	&-group {
		position: relative;
		&:not(:first-child) {
			margin-top: 1.6rem; } }
	&-submit {
		margin-top: 3rem;
		.btn {
			margin: 0 auto; } }
	&-control {
		background: #F3F3F3;
		border: .1rem solid #D9D9D9;
		height: 4rem;
		line-height: 3.8rem;
		padding: 0 2.4rem;
		color: #000000;
		border-radius: 2rem;
		font-size: 1.6rem; }
	&-checkbox {
		@extend .hidden;
		+ .form-label {
			cursor: pointer;
			padding-left: 3rem;
			position: relative;
			margin-bottom: 0;
			display: block;
			&:before, &:after {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0; }
			&:before {
				content: '';
				@include size(1.4rem);
				border: 0.1rem solid #000000; }
			&:after {
				content: '\2714';
				@include size(1.4rem);
				line-height: 1.4rem;
				text-align: center;
				opacity: 0;
				transition: .25s; } }
		&:checked + .form-label:after {
			opacity: 1; } }
	&-radio {
		@extend .hidden;
		+ .form-label {
			cursor: pointer;
			padding-left: 3rem;
			position: relative;
			margin-bottom: 0;
			display: block;
			&:before, &:after {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				border-radius: 100%; }
			&:before {
				@include size(1.4rem);
				border: 0.1rem solid #000000;
				left: 0; }
			&:after {
				@include size(.6rem);
				opacity: 0;
				transition: .25s;
				background-color: #000000;
				left: .4rem; } }
		&:checked + .form-label:after {
			opacity: 1; } } }
.section-heading {
	margin-bottom: 3rem;
	h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
		margin-bottom: 0; } }

// header
.header {
	padding: {
		top: 1.2rem;
		bottom: 0.2rem; }
	border-bottom: 0.1rem solid #b8b8b8;
	&-logo {
		margin-right: 3.2rem;
		&_link {
			display: block;
			font-size: 2.8rem;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 0.07rem;
			text-decoration: none!important; } }
	&-nav {
		margin-top: .8rem;
		&_link {
			display: block;
			font-size: 1.5rem; } }
	&-menu {
		&_link {
			font-size: 1.3rem;
			display: block;
			text-decoration: none !important;
			.icon {
				display: block;
				vertical-align: auto;
				top: auto;
				margin: 0 auto .7rem;
				transition: 0.25s; }
			&:hover {
				.icon {
					transform: translateY(-.25rem); } } } }
	&-hamburger {
		margin-top: 1rem; } }
// header

// promo
.promo {
	margin-bottom: 8rem;
	&-carousel {
		&_block {
			height: 80rem;
			max-height: calc(100vh - 6.1rem);
			position: relative;
			color: #ffffff;
			background-position: right center; }
		&_link {
			position: absolute;
			bottom: 3.1rem;
			color: #ffffff;
			left: 50%;
			transform: translateX(-50%);
			text-decoration: underline;
			font-size: 1.8rem;
			&:hover {
				text-decoration: none; } }
		.owl {
			&-dots {
				position: absolute;
				pointer-events: none;
				@extend .container;
				left: 50%;
				transform: translateX(-50%);
				bottom: 3.6rem;
				display: flex;
				justify-content: flex-end; }
			&-dot {
				display: block;
				@include size(.8rem);
				pointer-events: auto;
				border-radius: 100%;
				background-color: rgba(#ffffff, .65)!important;
				&:not(:first-child) {
					margin-left: 1.3rem; }
				span {
					display: none; }
				&.active, &:hover {
					background-color: #ffffff!important; } } } } }
// promo

// products
.products {
	margin-bottom: 8.5rem;
	&-box {
		margin-bottom: -2.7rem;
		> * {
			margin-bottom: 2.7rem; } }
	&-block {
		text-decoration: none !important;
		display: block;
		&:hover {
			.image:before {
				transform: scale(1.05, 1.05); } }
		.image {
			padding-bottom: 120%;
			margin-bottom: 2rem; }
		&_title, &_price {
			display: block; }
		&_title {
			font-size: 1.7rem; }
		&_price {
			font-size: 1.8rem; } }
	&-favorite {
		.icon {
			display: block;
			top: auto;
			vertical-align: auto;
			transition: 0.25s; }
		&:hover {
			.icon {
				transform: translateY(-.25rem); } } }
	&-color {
		&_list {
			margin: {
				left: 0;
				right: 0; } }
		&_item {
			display: block;
			@include size(1.3rem);
			border-radius: 100%; } } }
// products

// model
.model {
	margin-bottom: 9.8rem;
	&-box {
		> .model-block {
			.image {
				height: 100%;
				padding-bottom: 0;
				.model-block_name {
					left: 7.3rem; } } } }
	&-block {
		.image {
			position: relative;
			padding-bottom: 128%; }
		&_name {
			display: block;
			position: absolute;
			bottom: 4.5rem;
			left: 4.3rem;
			color: #ffffff;
			font-size: 2.2rem;
			text-transform: uppercase; } }
	&-title {
		padding: 1.8rem 13% 5.4rem 6rem;
		&_text {
			display: block;
			font-size: 3.9rem;
			line-height: 1.36;
			text-transform: uppercase; } } }
// model

// insta
.insta {
	margin-bottom: 9rem;
	&-box {
		margin-bottom: -2rem; }
	&-block {
		margin-bottom: 2rem;
		.image {
			padding-bottom: 100%;
			margin-bottom: 1.6rem; }
		.row {
			padding-left: 0.4rem;
			margin: {
				left: 0;
				right: 0; } }
		&_name {
			display: block;
			font-size: 1.7rem;
			font-weight: 400; }
		&_link {
			display: block;
			font-size: 1.7rem;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } } }
// insta

// item
.item {
	margin-bottom: 7.7rem;
	.container-fluid {
		padding: {
			left: 0;
			right: 0; }
		margin-bottom: 6.6rem;
		> .row {
			margin: {
				left: 0;
				right: 0; } } }
	&-carousel {
		&_block {
			padding-bottom: 118%; }
		.owl {
			&-nav {
				position: absolute;
				pointer-events: none;
				display: flex;
				padding: 0 2.3rem;
				justify-content: space-between;
				top: 50%;
				transform: translateY(-50%);
				width: 100%; }
			&-prev, &-next {
				display: block;
				@include size(2.5rem, 5.1rem);
				background: url(../img/icon/icon-arrow.png) center no-repeat!important;
				cursor: pointer;
				opacity: 0.8;
				transition: 0.25s;
				pointer-events: auto;
				&:hover {
					opacity: 1; }
				span {
					display: none; }
				&.disabled {
					opacity: 0; } }
			&-prev {
				transform: rotate(-180deg); } } }
	&-content {
		padding-left: 4.55%!important; }
	&-title {
		margin-bottom: 0; }
	&-info {
		display: block;
		color: #b2b2b2;
		text-transform: uppercase;
		margin-bottom: .7rem; }
	&-price {
		display: block;
		font-size: 2rem;
		text-transform: uppercase; }
	&-color {
		margin: {
			top: 4rem;
			bottom: 4.3rem; }
		&_title {
			display: block;
			font-size: 1.5rem;
			margin-bottom: .9rem; }
		.form-radio + .form-label {
			padding-left: 0;
			@include size(2rem);
			border-radius: 100%;
			&:before {
				display: none; }
			&:after {
				z-index: -1;
				background-color: #d0d0d0;
				left: -0.4rem;
				@include size(2.8rem); } }
		&_text {
			display: block;
			color: #b2b2b2;
			font-size: 1.5rem; } }
	&-size {
		display: flex;
		align-items: center;
		height: 4.8rem;
		border: 0.2rem solid #d0d0d0;
		font-size: 1.5rem;
		margin-bottom: 3.9rem;
		padding: {
			left: 1.9rem;
			right: 1.9rem; } }
	&-favorite {
		width: 100%;
		position: relative;
		margin-bottom: 2.7rem;
		.icon {
			position: absolute;
			right: 1.5rem;
			top: 50%;
			vertical-align: auto;
			transform: translateY(-50%); } }
	&-share {
		display: block;
		font-size: 1.5rem;
		.icon {
			transition: 0.25s; }
		&:hover {
			.icon {
				transform: translateX(.25rem); } } }
	&-reviews {
		margin: {
			top: 5.1rem;
			bottom: 1.6rem; }
		&_box {
			margin-bottom: 1.1rem; }
		&_text {
			display: block;
			font-size: 1.5rem;
			color: #b2b2b2; } }
	&-description {
		p {
			max-width: 48rem; }
		+ * {
			padding-left: 5.8%; } }
	&-tags {
		margin-bottom: -1rem;
		> * {
			margin-bottom: 1rem; }
		&_item {
			display: flex;
			height: 3.2rem;
			justify-content: center;
			align-items: center;
			padding: {
				left: 2.3rem;
				right: 2.3rem; }
			color: #ffffff;
			font: 0.5rem;
			background-color: #c4c4c4; } }
	p {
		margin-bottom: 2rem;
		&:last-child {
			margin-bottom: 0; } }
	.link-decorated {
		font-size: 1.5rem; } }
// item

// page
.page {
	&-control {
		display: flex;
		justify-content: flex-end;
		margin-bottom: 9.5rem;
		&_item {
			color: #b2b2b2;
			&:hover {
				color: #b2b2b2; }
			&:not(:first-child) {
				position: relative;
				margin-left: 1.6rem;
				&:before {
					content: '/';
					font-size: 1.4rem;
					font-weight: 500;
					color: #b2b2b2;
					pointer-events: none;
					position: absolute;
					left: -1rem;
					top: 50%;
					transform: translateY(-50%); } } } } }
// page

// catalog
.catalog {
	margin-bottom: 5.7rem;
	&-heading {
		position: relative;
		padding: {
			top: 5rem;
			bottom: 3.2rem; }
		.h4, &_text {
			text-align: center; }
		.h4 {
			margin-bottom: .7rem;
			text-transform: uppercase; }
		&_text {
			display: block;
			font-size: 1.6rem; } }
	&-functional {
		position: absolute;
		right: 0;
		top: calc(50% + .1rem);
		transform: translateY(-50%);
		font-size: 1.5rem; }
	&-sort {
		&_control {
			display: block;
			cursor: pointer; } }
	&-view {
		display: flex;
		&_text, &_control {
			display: block; }
		&_text {
			margin-right: 0.8rem; }
		&_control {
			&:not(:last-child) {
				margin-right: 1.4rem;
				position: relative;
				&:after {
					content: '';
					position: absolute;
					@include size(.1rem, 1.4rem);
					right: -0.7rem;
					top: 50%;
					transform: translateY(-50%);
					background-color: #000000;
					display: block; } }
			&:not(.is-active) {
				cursor: pointer; }
			&.is-active {
				color: #b3b3b3; }
			position: relative; } }
	.products {
		&-box {
			padding-right: 6%; }
		&-block {
			.image {
				margin-bottom: 1.4rem;
				padding-bottom: 129%; }
			.row {
				margin: {
					left: 0;
					right: 0; } }
			&_name, &_price {
				font-size: 1.6rem; } }
		&-color_list {
			margin: {
				left: 0;
				right: 0;
				top: .6rem; } } } }
// catalog

// aside
.aside {
	margin: {
		top: -.7rem;
		bottom: 3rem; }
	&-block {
		&:not(:first-child) {
			margin-top: 4.4rem; }
		&_title {
			display: block;
			font-size: 2rem;
			margin-bottom: 2.2rem; } }
	&-control {
		display: block;
		margin-bottom: 2rem;
		text-align: center;
		text-transform: uppercase;
		cursor: pointer; }
	&-price_slider {
		margin-bottom: 3rem; } }
.color {
	&-list {
		padding-left: 2.1rem;
		&_item {
			&:not(:first-child) {
				margin-top: 1.2rem; } } } }
// aside

// category
.category {
	&-list {
		margin-bottom: 4.3rem;
		&_item {
			ul {
				padding-left: 2.2rem;
				margin-top: 1.5rem;
				li {
					&:not(:first-child) {
						margin-top: .8rem; }
					a {
						font-size: 1.6rem; } } } }
		&_link {
			font-size: 2rem; }
		.is-active {
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } } }
// category

// footer
.footer {
	border-top: 0.1rem solid #b8b8b8;
	padding-top: 8.5rem;
	&-title {
		display: block;
		font-size: 1.7rem;
		margin-bottom: 3.5rem; }
	&-form {
		position: relative; }
	&-control {
		border-bottom: 0.2rem solid #000000;
		padding-bottom: 1.6rem;
		font-weight: 500;
		width: 37.7rem; }
	&-send {
		display: block;
		position: absolute;
		bottom: 2rem;
		right: 0;
		z-index: 2; }
	&-social {
		&_link {
			display: flex;
			justify-content: center;
			align-items: center;
			@include size(2.6rem); } }
	&-list {
		color: #474747;
		font-size: 1.6rem;
		line-height: 1.63;
		&_link {
			color: #474747; } }
	&-nav {
		margin: {
			top: 7.1rem;
			bottom: 4.8rem; } }
	&-copy {
		max-width: 75rem;
		margin: 0 auto 2rem;
		&_text {
			text-align: center;
			display: block;
			color: #9e9e9e;
			line-height: 1.25;
			font-weight: 400; } } }
// footer

@import "media";
