@import "vars";


/*==========  Desktop First  ==========*/
// ≥1800px
@include media-breakpoint-down(xxl) {
	.catalog {
		.products-box {
			padding-right: 0; } }
	.page-control {
		margin-bottom: 5rem; } }
// ≥1600px
@include media-breakpoint-down(xl) {
	.model-title {
		padding: 1.4rem 0; }
	.row-xxl {
		margin: {
			left: -1rem;
			right: -1rem; }
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } }
	.item-content {
		padding-left: 1rem!important; }
	.page-control {
		margin-bottom: 1.5rem; }
	.item-color {
		margin: {
			top: 1.5rem;
			bottom: 1.5rem; } }
	.item-reviews {
		margin: {
			top: 1.5rem;
			bottom: 0; } } }
// ≥1200px
@include media-breakpoint-down(lg) {
	.model-title_text {
		font-size: 3rem; }
	.catalog-functional {
		position: relative;
		right: auto;
		top: auto;
		transform: translateY(0);
		margin-top: 2rem; }
	.item-content {
		margin-top: 3rem;
		padding-left: 0rem!important; }
	.item-description + * {
		padding-left: 1rem; } }
// ≥992px
@include media-breakpoint-down(md) {
	.header {
		position: relative;
		&-nav {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			transition: 0.25s;
			border-top: 0.1rem solid #b8b8b8;
			border-bottom: 0.1rem solid #b8b8b8;
			left: 0;
			right: 0;
			margin-top: 0;
			position: absolute;
			top: 100%;
			background-color: #ffffff;
			z-index: 2;
			padding: {
				top: 1rem;
				bottom: 1rem; }
			&_list {
				margin: {
					right: -1rem;
					left: -1rem; } } } }
	.is-menu .header-nav {
		opacity: 1;
		visibility: visible;
		pointer-events: auto; }
	.model {
		&-title {
			padding: 0;
			&_text {
				font-size: 2.6rem; } }
		&-block_name {
			left: 0!important;
			right: 0;
			text-align: center;
			bottom: 3rem;
			padding: 0 1.5rem;
			font-size: 1.6rem; } }
	.footer {
		&-control {
			width: 100%; }
		.row-s {
			margin: {
				left: -1rem;
				right: -1rem; }
			> * {
				padding: {
					left: 1rem;
					right: 1rem; } } } } }
// ≥768px
@include media-breakpoint-down(sm) {
	.model-title_text {
		margin-bottom: 3rem; }
	.model-box>.model-block {
		margin-top: 1.4rem;
		.image {
			padding-bottom: 60%; } }
	.insta-block {
		.row {
			margin: {
				left: -1rem;
				right: -1rem; }
			padding-left: 0; }
		text-align: center; }
	.aside-content {
		display: none; }
	.page-control {
		margin-bottom: 3rem; }
	.item .container-fluid {
		margin-bottom: 3rem; }
	.page-control {
		justify-content: center; } }
// ≥576px
@include media-breakpoint-down(xs) {
	.header {
		padding: {
			top: .7rem;
			bottom: .7rem; }
		&-logo {
			margin-right: 0; }
		.container {
			.row {
				margin: {
					right: -.5rem;
					left: -.5rem; }
				> * {
					padding: {
						left: 0.5rem;
						right: 0.5rem; } } } }
		&-menu {
			&_list {
				margin: {
					right: -.5rem;
					left: -.5rem; }
				> * {
					padding: {
						left: 0.5rem;
						right: 0.5rem; } } }
			&_link {
				.icon {
					margin-bottom: 0; } } }
		&-hamburger {
			margin-top: 0; } }
	.footer-title {
		margin-bottom: 1rem;
		text-align: center; }
	.header-nav {
		&_list {
			margin: {
				left: -0.5rem;
				right: -0.5rem; } }
		&_item {
			padding: {
				left: .5rem;
				right: .5rem; } } }
	.promo-carousel {
		&_block {
			max-height: 40vh; }
		&_link {
			white-space: nowrap;
			bottom: 5rem; }
		.owl-dots {
			justify-content: center; } }
	.catalog-functional {
		.row-l {
			margin: {
				left: -.5rem;
				right: -.5rem; }
			> * {
				padding: {
					left: .5rem;
					right: .5rem; } } } }
	.item-tags {
		margin: {
			left: -1rem;
			right: -1rem; }
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } }
	.item-reviews {
		margin-top: 1rem; }
	.catalog .products-block .image {
		margin-bottom: -3.6rem; }
	.catalog .products-block_name,
	.catalog .products-block_price {}
	.products-block_title,
	.products-block_price {
		font-size: 1.4rem; }
	.products-block .image {
		margin-bottom: -3.6rem; }
	.catalog .products-block .row {
		margin: {
			left: -1rem;
			right: -1rem; } }
	.row-xl:not(.header-nav_list) {
		margin: {
			left: -1rem;
			right: -1rem; }
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } } }
// ≥410px
@include media-breakpoint-down(xxs) {
	.header {
		&-logo_link {
			font-size: 2.4rem; }
		&-nav {
			padding: {
				top: 0;
				bottom: 0; }
			&_item {
				&:not(:first-child) {
					border-top: .1rem solid rgba(#b8b8b8, .3); } }
			&_link {
				text-align: center;
				padding: {
					top: 1rem;
					bottom: 1rem; } } } }
	a.link-decorated {
		font-size: 1.4rem; }
	.products, .promo, .item, .catalog, .insta, .model {
		margin-bottom: 3rem; }

	.footer {
		padding-top: 3rem;
		&-nav {
			margin-top: 3rem; }
		&-list {
			text-align: center; } } }

/*==========  Mobile First  ==========*/

// ≤576px
@include media-breakpoint-up(sm);

// ≤768px
@include media-breakpoint-up(md) {
	.model {
		> .container {
			width: 100%;
			max-width: none;
			padding: {
				left: 0;
				right: 0; }
			.row-s {
				margin: {
					left: 0;
					right: 0; }
				> * {
					&:first-child {
						padding-left: 0; }
					&:last-child {
						padding-right: 0; } } } } }
	.aside {
		flex: 0 0 23.9rem;
		max-width: 23.9rem; } }
// ≤992px
@include media-breakpoint-up(lg);

// ≤1200px
@include media-breakpoint-up(xl) {
	.footer .container {
		max-width: 105.4rem; }
	#grid {
		&-2 {
			> * {
				max-width: 50%;
				flex: 0 0 50%; } }
		&-3 {
			> * {
				max-width: 33.33333%;
				flex: 0 0 33.33333%; } }
		&-4 {
			> * {
				max-width: 25%;
				flex: 0 0 25%; } } } }
